@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.intro{margin:5vh;width:clamp(250px, 70vw, 2000px);box-shadow:rgba(0,0,0,.2) 10px 10px 10px,rgba(0,0,0,.1) -10px -10px 10px;display:flex;flex-direction:column;align-items:center;background-color:rgba(0,0,0,.2);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);color:#fff;font-family:"Josefin Sans",sans-serif;border-radius:4px}.intro img{margin:3vh 0;width:70%;max-width:300px}.intro .intro__info{height:100%;display:flex;flex-direction:column;align-items:center}.intro .intro__info .name{font-size:4rem;color:#182947}@media only screen and (max-width: 1000px){.intro .intro__info .name{font-size:2rem}}.intro .intro__info .aka{margin:10px 0px;display:flex;align-items:flex-start;font-size:1.4rem}@media only screen and (max-width: 1000px){.intro .intro__info .aka{font-size:1rem}}.intro .intro__info .nickname{font-size:3rem;color:#0cb2ff}@media only screen and (max-width: 1000px){.intro .intro__info .nickname{font-size:1.7rem}}.intro .intro__info .info{margin:20px;display:grid;place-items:center;font-size:2rem}
ul{list-style-type:none;margin:0;padding:0}a,a:hover,a:visited,a:active{color:inherit;text-decoration:none}.navspace{background-color:rgba(0,0,0,.1);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);color:#fff;box-shadow:rgba(0,0,0,.9) 3px 3px 10px,rgba(255,255,255,.06) -5px -5px 10px;border-radius:4px}.navspace .links{display:flex}.navspace .links>*{padding:10px 10px}
.mainbg{height:100vh;width:100vw;background-image:url(/react-portfolio/static/media/background.914506f6.jpg);background-position:center;background-repeat:no-repeat;background-size:cover;display:flex;flex-direction:column;align-items:center}
